import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import 'lib-flexible' //rem转换
import axios from 'axios'
import '../static/css/main.css' //css样式修改
import echarts from 'echarts' //k线图
import Vant from 'vant'; //vant
import 'vant/lib/index.css';
import {
	Lazyload
} from 'vant';
import {
	Toast
} from 'vant';
Toast.setDefaultOptions({
	duration: 1000
}); //设置提示时间
Vue.use(Vant);
import {
	Dialog
} from 'vant';
Vue.use(Dialog);
Vue.use(Lazyload, '');
Vue.use(Toast);

import VueClipboard from 'vue-clipboard2' //复制
Vue.use(VueClipboard)

Vue.prototype.$http = axios;
Vue.prototype.$get = get;
Vue.prototype.$post = post;
Vue.prototype.$echarts = echarts
Vue.prototype.$jq = jiequ;
Vue.prototype.$time = times;
Vue.prototype.$days = days;

// 过滤器,保留两位小数
Vue.filter("numFilter", function (value) {
	let transformVal = Number(value).toFixed(2);
	return transformVal;
});

const baseUrl = 'http://www.umihuoban.com/api'
// const baseUrl = 'http://192.168.0.194:8883/api'

axios.defaults.headers.post['Content-Type'] = 'application/json';
axios.interceptors.request.use(
	config => {
		if (localStorage.getItem('token')) {
			//token字段是要和后端协商好的
			config.headers.common["token"] = localStorage.getItem('token');
			// debugger
		}
		return config;
	},
	error => {
		return Promise.reject(error);
	});

// http response 拦截器
axios.interceptors.response.use(
	response => {
		if (response.data.code == 999) {
			localStorage.clear()
			router.replace({
				path: "/login",
				querry: {
					redirect: router.currentRoute.fullPath
				} //从哪个页面跳转
			})
		}
		// }
		return response;
	},
	error => {
		return Promise.reject(error)
	}
)


function get(url, params = {}) { //get 封装

	return new Promise((resolve, reject) => {
		axios.get(baseUrl + url, {
			params: params
		}).then(response => {
			resolve(response.data);
		}).catch(err => {
			reject(err)
		})
	})


}

function post(url, data = {}) { //post封装
	return new Promise((resolve, reject) => {
		axios.post(baseUrl + url, data)
			.then(response => {
				resolve(response.data);
			}, err => {
				reject(err)
			})
	})
}
document.addEventListener('plusready', function () { //物理按键
	var webview = plus.webview.currentWebview();
	plus.key.addEventListener('backbutton', function () {
		webview.canBack(function (e) {
			if (e.canBack) {
				webview.back(); //返回上一页
			} else {
				webview.close(); //关闭应用
			}
		})
	});
});


function jiequ(str) { //截取小数点后两位
	if (String(str).indexOf(".") > -1) {
		var temp = Number(str);
		temp = Math.floor(temp * 1000) / 1000;
		temp = temp.toFixed(3);
		return temp
	} else {
		return str
	}
}

function times(timestamp) { //转换时间戳
	let date = new Date(timestamp * 1000); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
	let Y = date.getFullYear() + '-';
	let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
	let D = (date.getDate() < 10 ? '0' + date.getDate() + ' ' : date.getDate() + ' ');
	let h = (date.getHours() < 10 ? '0' + date.getHours() + ':' : date.getHours() + ':');
	let m = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes());
	let s = date.getSeconds();
	return Y + M + D + h + m;
}

function days(num) { //转换日期-年月日
	var time = new Date(num);
	var Y = time.getFullYear() + '-';
	var M = (time.getMonth() + 1 < 10 ? '0' + (time.getMonth() + 1) : time.getMonth() + 1) + '-';
	var D = time.getDate() < 10 ? '0' + time.getDate() : time.getDate();
	return Y + M + D;
}

new Vue({
	router,
	store,
	render: h => h(App)
}).$mount("#app");